<template>
  <div class="share-specialgov-detail full-screen">
    <div class="page-panel">
      <pem-scroll-view class="scroll-view" ref="scroll"
        :data="list"
        :probeType="0"
        :pullup=true
        @pullUpLoad="listMore">
        <div class="container">
          <div class="special-top" @click="judgeGuide">
            <img class="top-bg" v-if="!basicObj.bannerimg" src="./bg_top@2x.png"/>
            <img class="top-bg" v-if="basicObj.bannerimg" :src="basicObj.bannerimg"/>
          </div>
          <!-- 显示类型 -->
          <ul class="types">
            <li @click="switchTypes"><span :class="{'on': showType === 'detail' }">政府简介</span></li>
            <li @click="switchTypes"><span :class="{'on': showType !== 'detail' }">项目列表</span></li>
          </ul>
          <!-- 详情列表 -->
          <ul class="detail-ul" v-if="showType === 'detail'" @click="judgeGuide">
            <li class="detail-li" v-for="(item, index) in descArr" :key="index">
              <h3 v-if="item.type === 100">{{ item.content }}</h3>
              <div v-if="item.type === 200">{{ item.content }}</div>
              <img v-if="item.type === 300" :src="item.content"/>
            </li>
          </ul>
          <!-- 项目列表 -->
          <ul class="special-project-list" v-if="showType !== 'detail'" @click="judgeGuide">
            <li class="item"
              v-for="item in list"
              :key="item.projectlibzj">
              <img class="logo" :src="item.logo"  @error="defaultLogo">
              <div class="name-wrapper clearfix">
                <span class="name">{{ item.name }}</span>
                <span class="new" v-if="item.weekflag && !item.viewflag"></span>
                <span class="float-right money">{{ item.money }}</span>
              </div>
              <div class="desc">{{ item.descriptionmemo }}</div>
              <div class="msgs clearfix">
                <span class="turn">{{ item.turn }}</span>
                <small v-if="item.fields">|</small>
                <span class="field">{{ item.fields }}</span>
                <div class="float-right identify">
                  <img class="badge" v-for="(icon, index) in item.iconList" :key="index" :src="icon.imgurl" />
                </div>
              </div>
            </li>
          </ul>
        </div>
      </pem-scroll-view>
      <div class="footer-open" @click="judgeGuide">
        <img class="logo" src="http://m.pemarket.com.cn/static/logo.png" alt="">
        <h4>项目工场</h4>
        <p>找项目、找投资就到项目工场</p>
        <div class="btn">打开</div>
      </div>
      <div class="guide-open-browser" v-if="showGuideMask" @click="closeMask">
        <div class="guide-img"></div>
      </div>
      <pem-loading v-show="isLoading"></pem-loading>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import PemScrollView from 'components/scroll-view/scroll-view'
import PemLoading from 'components/pem-loading/pem-loading'
import { MiniSiteApi, ImgPath } from 'api'
import { setTitle, device } from 'common/js/util'

import tempLogoUrl from 'common/image/icon-project-Default_graph@2x.png'

export default {
  data () {
    return {
      id: this.$route.query.id,
      rootImgUrl: ImgPath,
      showGuideMask: false,
      showType: 'detail',
      currentPage: 1,
      pageSize: 100,
      basicObj: {
        title: '',
        bannerimg: null
      },
      descArr: [],
      list: [],
      isMore: true,
      isLoading: false
    }
  },
  components: {
    PemScrollView,
    PemLoading
  },
  created () {
    this._initData()
    this.wxShare({})
  },
  methods: {
    _initData () {
      if (!device.isWeixin() && device.isAndroid()) {
        window.location.href = `pemarket://www.pemarket.com.cn/attr?type=8&id=${this.$route.query.id}`
      }
      this.getData()
    },
    getData () {
      this._getAppShareSpecialGovBasic()
      this._getAppShareSpecialGovDetail()
    },
    switchTypes () {
      if (this.showType === 'detail') {
        this.showType = 'projectlist'
        this._getAppShareSpecialGovProjects('init')
      } else {
        this.showType = 'detail'
        this._getAppShareSpecialGovDetail()
      }
    },
    listMore () {
      console.log('rrrryyy')
      // if (!this.isLoading && this.isMore) {
      //   this._getAppShareSpecialGovProjects('more')
      // }
    },
    judgeGuide () {
      if (device.isWeixin()) {
        if (device.isIOS()) {
          window.location.href = `https://go.pemarket.com.cn/download-guide?type=8&id=${this.$route.query.id}`
        } else {
          this.showGuideMask = true
        }
      } else {
        this.$router.push('/download')
      }
    },
    closeMask () {
      this.showGuideMask = false
    },
    refresh () {
      this.$refs.scroll.refresh()
    },
    defaultLogo (e) {
      e.target.src = require('common/image/icon-project-Default_graph@2x.png')
    },
    _getAppShareSpecialGovBasic () {
      MiniSiteApi.getAppShareSpecialGovBasic({id: this.id}).then(data => {
        if (+data.resultcode === 1) {
          if (data.specialname) {
            this.basicObj.title = data.specialname
            setTitle(data.specialname)
          }
          if (data.bannerimg) {
            this.basicObj.bannerimg = ImgPath + data.bannerimg
          }
          this.wxShare({
            title: data.sharetitle,
            content: data.sharecontent,
            imgUrl: ImgPath + data.sharelogo
          })
        }
      })
    },
    _getAppShareSpecialGovDetail () {
      MiniSiteApi.getAppShareSpecialGovDetail({id: this.id}).then(data => {
        if (+data.resultcode === 1) {
          this.descArr = (data.resultList || []).map(item => {
            if (item.type === 300) {
              item.content = ImgPath + item.content
            }
            return item
          })
          this.refresh()
        }
      })
    },
    _getAppShareSpecialGovProjects (type = 'init') {
      this.isLoading = true
      if (type === 'init') {
        this.currentPage = 1
      }
      MiniSiteApi.getAppShareSpecialGovProjects({
        id: this.id,
        currentPage: this.currentPage,
        pageSize: this.pageSize
      }).then(data => {
        this.isLoading = false
        if (+data.resultcode === 1) {
          this.currentPage = (+data.currentPage || 1) + 1
          if (+data.totalPage <= +data.currentPage) {
            this.isMore = false
          }
          let _list = (type === 'init') ? [] : this.list
          this.list = _list.concat(data.resultList || []).map(item => {
            item.logo = item.logo ? (ImgPath + item.logo) : tempLogoUrl
            item.money = '暂不透露'
            if (!item.raisefundstatus && item.raisefunds && item.currencytype && item.raisefunds > 0) {
              item.money = item.currencytype + item.raisefunds + '万'
            }
            if (item.iconList) {
              item.iconList = item.iconList.map(icon => {
                icon.imgurl = ImgPath + icon.imgurl
                return icon
              })
            }
            if (item.fieldList) {
              item.fields = item.fieldList.map(field => {
                return field.fieldname
              }).join('、')
            }
            return item
          })
          this.refresh()
        }
      })
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/mixin"
  .share-specialgov-detail
    background: #ffffff
    .guide-open-browser
      position: fixed
      left: 0
      top: 0
      right: 0
      bottom: 0
      z-index: 1000
      opacity: 0.7
      background: #000000
      .guide-img
        width: 5.12rem
        height: 2.54rem
        margin: 0.48rem auto 0
        bg-image('./bg_guide')
        background-size: 100% 100%
        background-repeat: no-repeat
    .container
      padding-bottom: 1.7rem
      .special-top
        position: relative
        box-sizing: border-box
        height: 3.8rem
        padding-top: 1.08rem
        text-align: center
        .top-bg
          position: absolute
          left: 0
          top: 0
          width: 100%
          height: 100%
          z-index: -1
        .top-logo
          display: block
          width: 1.12rem
          height: 1.12rem
          border-radius: 0.08rem
          margin: 0 auto 0.28rem
      .types
        height: 0.96rem
        font-size: 0
        border-bottom: 0.5px solid #ECECEC;
        li
          display: inline-block 
          width: 50%
          text-align: center
          span
            position: relative
            display: inline-block
            height: 0.96rem
            line-height: 0.96rem
            font-family: PingFangSC-Regular;
            font-size: 0.28rem;
            color: #999999;
            padding: 0 5px
            &.on
              color: #333333;
              &:after
                position: absolute
                content: ''
                left: 0
                right: 0
                bottom: 0
                height: 0.04rem
                background: #FFC131;
      .detail-ul
        padding: 0 0.3rem 0.3rem
        h3
          padding-top: 0.4rem
          font-family: PingFangSC-Medium;
          font-size: 0.32rem
          font-weight: bold
          color: #333333;
          line-height: 0.44rem
        div
          padding-top: 0.3rem
          font-family: PingFangSC-Regular;
          font-size: 0.26rem
          color: #666666;
          line-height: 0.42rem
        img
          width: 100%
          margin-top: 0.3rem
      .special-project-list
        padding: 0 0.3rem
        .item
          position: relative
          padding: 0.28rem 0 0.28rem 0.84rem
          border-bottom: 0.5px solid #EEEEEE
          &:last-child
            border-bottom: none
          .logo
            position: absolute
            left: 0
            top: 0.32rem
            width: 0.64rem
            height: 0.64rem
            border-radius: 0.08rem
          .name-wrapper
            font-size: 0
            span
              vertical-align: middle 
            .name
              font-family: PingFangSC-Medium
              font-size: 0.32rem
              font-weight: bold
              color: #333333
              letter-spacing: 0.54px
              line-height: 0.44rem
            .new
              display: inline-block
              width: 0.34rem
              height: 0.28rem
              margin-left: 0.06rem
              bg-image('./bg_new')
              background-size: 100% 100%
              background-repeat: no-repeat
            .money
              font-family: DIN-Medium
              font-size: 0.28rem
              color: #FF897D;
              letter-spacing: -0.09px;
              line-height: 0.34rem
          .desc
            font-family: PingFangSC-Regular
            font-size: 0.22rem
            color: #A7A7A7
            letter-spacing: -0.07px
            line-height: 0.34rem
            padding: 0.02rem 0 0.12rem
          .msgs
            font-size: 0
            .turn, .field
              font-family: PingFangSC-Regular
              font-size: 0.24rem
              color: #666666
              letter-spacing: -0.07px
              line-height: 0.34rem
            small 
              font-size: 0.22rem
              color: #C9C9C9
              margin: 0 0.1rem
              vertical-align: super
            .identify
              .badge
                height: 0.28rem
                margin-right: 0.1rem
                &:last-child
                  margin-right: 0
    .footer-open
      position: fixed
      left: 0
      bottom: 0
      right: 0
      height: 1.2rem
      border-top: 0.5px solid #f3f3f3
      background: #ffffff
      .logo
        position: absolute 
        left: 0.46rem
        top: 0.2rem
        height: 0.8rem
        width: 0.8rem
        border-radius: 0.08rem;
      h4
        position: absolute 
        left: 1.42rem
        top: 0.16rem
        font-family: PingFangSC-Medium
        font-size: 0.32rem
        font-weight: bold
        color: #41403E
        line-height: 0.44rem
      p
        position: absolute 
        left: 1.42rem
        bottom: 0.2rem
        font-family: PingFangSC-Regular
        font-size: 0.26rem
        color: #8B8B8B
        line-height: 0.36rem
      .btn
        position: absolute
        top: 0.32rem
        right: 0.22rem
        width: 1.56rem
        height: 0.6rem
        line-height: 0.6rem
        background: #FFC131
        border-radius: 0.04rem
        font-family: PingFangSC-Regular
        font-size: 0.3rem
        color: #41403E
        letter-spacing: -0.3px
        text-align: center
</style>


